export default {"name":"日本茶道","img":"\\abroads\\japan\\rbcd.jpg","desc":["日本茶道是在日本一种仪式化的、为客人奉茶之事。原称为“茶汤”。日本茶道和其他东亚茶仪式一样，都是一种以品茶为主而发展出来的特殊文化，但内容和形式则有别。","日本茶道源自中国。现在日本的煎茶道、中国台湾地区的泡茶道都来源于中国广东潮州的工夫茶，现在的日本茶道分为抹茶道与煎茶道两种，但茶道一词所指的是较早发展出来的抹茶道。","日本茶道是在“日常茶饭事”的基础上发展起来的，它将日常生活行为与宗教、哲学、伦理和美学熔为一炉，成为一门综合性的文化艺术活动。它不仅仅是物质享受，而且通过茶会，学习茶礼，陶冶性情，培养人的审美观和道德观念。"],"menus":[{"name":"简介","content":[{"text":"日本茶道是在日本一种仪式化的、为客人奉茶之事。原称为“茶汤”。日本茶道和其他东亚茶仪式一样，都是一种以品茶为主而发展出来的特殊文化，但内容和形式则有别。"},{"text":"茶道历史可以追溯到13世纪。最初是僧侣用茶来集中自己的思想，唐代赵州从谂禅师曾经以“吃茶去”来接引学人；后来才成为分享茶食的仪式。现在日本的煎茶道、中国台湾地区的泡茶道都来源于中国广东潮州的工夫茶，现在的日本茶道分为抹茶道与煎茶道两种，但茶道一词所指的是较早发展出来的抹茶道。"},{"text":"现代的茶道，由主人准备茶与点心，还有水果招待客人，而主人与客人都按照固定的规矩与步骤行事。除了饮食之外，茶道的精神还延伸到茶室内外的布置；品鉴茶室的书画布置、庭园的园艺及饮茶的陶器都是茶道的重点。"}]},{"name":"基本介绍","content":[{"text":"茶道有繁琐的规程，茶叶要碾得精细，茶具要擦得干净，主持人的动作要规范，既要有舞蹈般的节奏感和飘逸感，又要准确到位。"},{"text":"茶道品茶很讲究场所，一般均在茶室中进行。接待宾客时，待客人入座后，由主持仪式的茶师按规定动作点炭火、煮开水、冲茶或抹茶，然后依次献给宾客。客人按规定须恭敬地双手接茶，先致谢，尔后三转茶碗，轻品、慢饮、奉还。点茶、煮茶、冲茶、献茶，是茶道仪式的主要部分，需要专门的技术和训练。饮茶完毕，按照习惯，客人要对各种茶具进行鉴赏，赞美一番。最后，客人向主人跪拜告别，主人热情相送。"},{"text":"日本茶道是在“日常茶饭事”的基础上发展起来的，它将日常生活与宗教、哲学、伦理和美学联系起来，成为一门综合性的文化艺术活动。它不仅仅是物质享受，主要是通过茶会和学习茶礼来达到陶冶性情、培养人的审美观和道德观念的目的。"},{"text":"正如桑田中亲说的：“茶道已从单纯的趣味、娱乐，前进为表现日本人日常生活文化的规范和理想。”"},{"text":"十六世纪末，千利休继承历代茶道精神，创立了日本正宗茶道。他提出的“和敬清寂”，用字简洁而内涵丰富。“清寂”是指冷峻、恬淡、闲寂的审美观；“和敬”表示对来宾的尊重。整个茶会期间，从主客对话到杯箸放置都有严格规定，甚至点茶者伸哪只手、先迈哪只脚、每一步要踩在榻榻米的哪个格子里也有定式，正是定式不同，才使现代日本茶道分成了二十来个流派。"},{"text":"十六世纪前的日本茶道还要繁琐得多，现代茶道是经过千利休删繁就简的改革才成为现在的样子。"}]},{"name":"历史起源","content":[{"text":"日本的茶道起源于中国广东潮州，具有东方文化之韵味。它有自己的形成、发展过程和特有的内蕴。"},{"text":"正如桑田中亲说的：“茶道已从单纯的趣味、娱乐，前进成为表现日本人日常生活文化的规范和理想。”"},{"text":"十六世纪末，千利休继承、汲取了历代茶道精神，创立了日本正宗茶道。他是茶道的集大成者。剖析利休茶道精神，可以了解日本茶道之一斑。村田珠光曾提出过“谨、敬、清、寂”为茶道精神，千利休只改动了一个字，以“和敬清寂”四字为宗旨，简洁而内涵丰富。“清寂”也写作“静寂”。它是指审美观。这种美的意识具体表现在“侘”字上。“侘（わび）”日语音为\"wabi\"，原有“寂寞”、“贫穷”、“寒碜”、“苦闷”的意思。平安时期“侘人”一词，是指失意、落魄、郁闷、孤独的人。到平安末期，“侘”的含义逐渐演变为“静寂”、“悠闲”的意思，成为很受当时一些人欣赏的美的意识。"},{"src":"\\abroads\\japan\\qianlixiu.jpg"},{"text":"这种美意识的产生，有社会历史原因和思想根源：平安末期至镰仓时代，是日本社会动荡、改组时期，原来占统治地位的贵族失势，新兴的武士阶层走上了政治舞台。失去天堂的贵族感到世事无常而悲观厌世，因此佛教净土宗应运而生。失意的僧人把当时社会看成秽土，号召人们“厌离秽土，欣求净土”。在这种思想影响下，很多贵族文人离家出走，或隐居山林，或流浪荒野，在深山野外建造草庵，过着隐逸的生活，创作所谓“草庵文学”，以抒发他们思古之幽情，排遣胸中积愤。这种文学色调阴郁，文风“幽玄”。"}]},{"name":"饮茶程序","content":[{"text":"日本茶道是必须遵照规则来进行喝茶活动，而茶道的精神，就是蕴含在这些看起来繁琐的喝茶程序之中。进入茶道部，有身穿朴素和服，举止文雅的女茶师礼貌地迎上前来，简短地解说：进入茶室前，必须经过一小段自然景观区。这是为了使茶客在进入茶室前，先静下心来，除去一切凡尘杂念，使身心完全融入自然。开宗明义的一番话，就能领略到了正宗茶道的不凡。"},{"text":"然后在茶室门外的一个水缸里用一长柄的水瓢盛水，洗手，然后将水徐徐送入口中漱口，目的是将体内外的凡尘洗净，然后，把一个干净的手绢，放入前胸衣襟内，再取一把小折扇，插在身后的腰带上，稍静下心后，便进入茶室。日本的茶室，面积一般以置放四叠半“榻榻米”为度，小巧雅致，结构紧凑，以便于宾主倾心交谈。茶室分为床间﹑客﹑点前﹑炉踏等专门区域。室内设置壁龛﹑ 地炉和各式木窗，右侧布“水屋”，供备放煮水﹑沏茶﹑品茶的器具和清洁用具。床间挂名人字画，其旁悬竹制花瓶﹐瓶中插花，插花品种和旁边的饰物，视四季而有不同，但必须和季节时令相配。每次茶道举行时，主人必先在茶室的活动格子门外跪迎宾客，虽然进入茶室后，强调不分尊卑，但头一位进茶室的必然是来宾中的一位首席宾客（称为正客），其他客人则随后入室。来宾入室后，宾主相互鞠躬致礼，主客面对而坐，而正客须坐于主人上手（即左边）。这时主人即去“水屋”取风炉﹑茶釜﹑水注﹑白炭等器物，而客人可欣赏茶室内的陈设布置及字画﹑鲜花等装饰。主人取器物回茶室后，跪于榻榻米上生火煮水，并从香盒中取出少许香点燃。在风炉上煮水期间﹐主人要再次至水屋忙碌，这时众宾客则可自由在茶室前的花园中散步。待主人备齐所有茶道器具时，这时水也将要煮沸了﹐宾客们再重新进入茶室﹐茶道仪式才正式开始。主人一般在敬茶前，要先品尝一下甜点心，大概是为避免空肚喝茶伤胃，而且抹茶可能会比较苦，所以先品尝一下点心，可以避免抹茶的苦涩。敬茶时，主人用左手掌托碗﹐右手五指持碗边﹐跪地后举起茶碗，恭送至正客前。待正客饮茶后，余下宾客才能一一依次传饮。饮时可每人一口轮流品饮，也可各人饮一碗，饮毕将茶碗递回给主人。主人随后可从里侧门内退出，煮茶，或让客人自由交谈。在正宗日本茶道里，是绝不允许谈论金钱、政治等世俗话题的，更不能用来谈生意，多是些有关自然的话题。"}]},{"name":"茶道礼仪","content":[{"text":"日本的茶道品茶是很讲究场所的，一般均在茶室中进行，茶室多起名为“某某庵”的雅号，有广间和小间之分。茶居室一般以“四叠半”（约为9平方米）为标准，大于“四叠半”的称为广间；小于“四叠半”的称为小间。茶居室的中间设有陶制炭炉和茶釜，炉前摆放着茶碗和各种用具，周围设主、宾席位以及供主人小憩用的床等。"},{"text":"接待宾客时，由专门的茶师按照规定的程序和规则依次点炭火、煮开水、冲茶或抹茶，然后依次献给宾客。点茶、煮茶、冲茶、献茶，是茶道仪式的主要部分，都要经过专门的训练。茶师将茶献给宾客时，宾客要恭敬地双手接茶，致谢，而后三转茶碗，轻品，慢饮，奉还，动作轻盈优雅。饮茶完毕，按照习惯和礼仪，客人要对各种茶具进行鉴赏和赞美。最后，客人离开时需向主人跪拜告别，主人则热情相送。"},{"text":"茶道品茶还分为“轮饮”和“单饮”两种形式。轮饮是客人轮流品尝一碗茶，单饮是宾客每人单独一碗茶。茶道还讲究遵循\"四规\",\"七则\".四规指\"和、敬、清、寂\",乃茶道之精髓。“和、敬”是指主人与客人之间应具备的精神、态度和礼仪。“清、寂”则是要求茶室和饮茶庭园应保持清静典雅的环境和气氛。七则指的是：提前备好茶，提前放好炭，茶室应保持冬暖夏凉，室内要插花保持自然清新的美，遵守时间，备好雨具，时刻把客人放在心上等等。"}]},{"name":"茶道茶事","content":[{"text":"日本人相当注重形式，茶道便是这样的一种体现。他们喜欢当着客人的面准备食物，像铁板烧，让客人不仅能吃到食物，还能学习到烹饪的方法，茶道也是如此。"},{"text":"日本茶人在举行茶会时均抱有“一期一会”的心态。这一词语出自江户幕府末期的大茶人井伊直弼所著的《茶汤一会集》。书中这样写到：“追其本源，茶事之会，为一期一会，即使同主同客可反复多次举行茶事，也不能再现此时此刻之事。每次茶事之会，实为我一生一度之会。由此，主人要千方百计，尽深情实意，不能有半点疏忽。客人也须以此世不再相逢之情赴会，热心领受主人的每一个细小的匠心，以诚相交。此便是：一期一会。”这种“一期一会”的观念，实质上就是佛教“无常”观的体现。佛教的无常观督促人们重视一分一秒，认真对待一时一事。当茶事举行时，主客均极为珍视，彼此怀着“一生一次”的信念，体味到人生如同茶的泡沫一般在世间转瞬即逝，并由此产生共鸣。于是与会者感到彼此紧紧相连，产生一种互相依存的感觉和生命的充实感。这是茶会之外的其他场合无法体验到的一种感觉。"},{"text":"茶事的种类繁多，古代有“三时茶”之说，即按三顿饭的时间分为早茶）、午茶、晚茶；现在则有“茶事七事”之说，即：早晨的茶事、拂晓的茶事、正午的茶事、夜晚的茶事、饭后的茶事、专题茶事和临时茶事。除此之外还有开封茶坛的茶事、惜别的茶事、赏雪的茶事、一主一客的茶事、赏花的茶事、赏月的茶事等等。每次的茶事都要有主题，比如某人新婚、乔迁之喜、纪念诞辰、或者为得到了一件珍贵茶具而庆贺等等。"},{"text":"茶会之前，主人要首先确定主客，即主要的客人，一般为身份较尊贵者，像千利休之于丰臣秀吉。确定了主客之后再确定陪客，这些陪客既要和主客比较熟悉又要和主客有一定的关系。决定客人之后便要开始忙碌的准备茶会了，这期间客人们会来道谢，因为准备工作的繁忙主人只需要在门前接待一下即可。一般茶会的时间为四个小时，太长容易导致客人疲惫，太短又可能无法领会到茶会的真谛。茶会有淡茶会（简单茶会）和正式茶会两种，正式茶会还分为“初座”和“后座”两部分。为了办好茶会，主人要东奔西跑的选购好茶、好水、茶花、做茶点心及茶食的材料等。茶会之前还要把茶室、茶庭打扫的干干净净，客人提前到达之后，在茶庭的草棚中坐下来观赏茶庭并体会主人的用心，然后入茶室就座，这叫“初座”。主人便开始表演添炭技法，因为整个茶会中要添。"},{"text":"茶会通常有纪录，纪录的内容包括与会众、壁龛装饰、茶具、饭菜、点心等情况，有时还加入与会众的谈话摘要和纪录者的评论。这种纪录叫“会记”。古代有很多著名茶会的会记流传下来，成为现代珍贵的资料，如《松屋会记》、《天王寺屋会记》、《今井宗久茶道记书拔》、《宗湛日记》等被称为四大会记。"}]},{"name":"茶道影响","content":[{"text":"由于寺院禅宗的影响，人们常常过分注意禅和佛教的联系。其实禅具有中国文化的背景，比之佛教，禅与儒家思想的关系更深。世界本来也许是有一个终极真理的，如果人能够完全理解这个真理，那么人世就是天堂。可惜人寿有限，我们短暂的一生不足以完全领悟所有的道，这个矛盾是人类所有哲学的根源问题。佛讲轮回，基督讲末日审判，阴阳家讲长生登仙，马克思讲共产主义。儒家的解决办法是薪尽火传，也就是前人根据自己的经验制定规则，后人通过遵循这些规则，可以从比前人更高的起点，去领悟人生，从而更加接近真理。然而后人怎能理解前人制定的准则呢？这些准则如何不成为对人的束缚呢？孔子也说，自己到了晚年才能随心所欲而不越矩。儒家在此与禅汇合，人必须先遵循一些规矩，修炼自己的本质，遏制欲望的目的是使欲望不必遏制，当人达到随心所欲而不越矩时，规矩就不存在了。人只有通过不断地拂拭内心，才能最终令尘埃无可染之处。所以茶道里禅的内涵，不在于什么“直心就是禅”，什么“喝茶去”，而是通过繁琐的规则来磨练人心，当这些定规不再令饮茶者厌烦，当饮茶人信手而为就符合茶道礼法时，才算领会了茶的真谛，才能喝到一杯好茶。繁复而熟练的礼法是为了使人超然物外，浓如苦药的茶汤正如人生，别出心裁的插花显示有限的生命背后人类生生不息的生命力，棒喝的偈语告诉人处处是真理。日本茶道，是用一种仪式来向人讲述禅的思想，正如参禅需要顿悟一样，其中蕴涵的那些人生的经验，需要饮茶者用生命的一段时光来领悟。"}]},{"name":"喝茶顺序","content":[{"text":"更衣 观赏茶庭 初茶 茶食 中立 浓茶 后炭 薄茶 退出 衔接"}]}]}